.section_tranding {
  padding-top: 80px;
}

.bacdrop {
  width: 100%;
  min-height: 150vw;
  margin: 0;
  padding: 0;
}
.link {
  transition-property: box-shadow;
  transition-duration: 200ms;
  transition-timing-function: linear;
}

.link:hover {
  box-shadow: 1px 2px 1px 0px #292a3314, 1px 3px 15px 3px #17101429,
    0px 1px 6px 6px #0f0f1114;
}
